* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.Choose_Apartment_Container {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 100px 0;
    transition: .4s;
    transition-delay: .100s;
    overflow: hidden;



    h1 {
        font-family: "Cormorant Garamond";
        text-transform: uppercase;
        color: #000;
        font-size: 64px;
        font-weight: 400;
        line-height: 1.09em;
        letter-spacing: .15em;

        @media(min-width:552px) and (max-width:768px) {
            font-size: 35px;
            text-align: center;
        }

        @media(min-width:320px) and (max-width:552px) {
            font-size: 18px;
            text-align: center;
        }
    }

    p {
        font-size: 13px;
        letter-spacing: .6em;
        font-family: Raleway;
        font-weight: 500;
        margin: 13px 0 0;
        text-transform: uppercase;
        font-weight: 500;
        line-height: 2.2em;
        color: #c28562;
        text-align: center;

        @media(min-width:320px) and (max-width:552px) {
            padding: 20px;
        }
    }

    .Choose_Complexes_Box {
        width: 100%;
        height: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        padding: 60px;

        @media(min-width:320px) and (max-width:768px) {
            padding: 40px 10px;
        }

        .Complex {
            width: 420px;
            height: auto;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            margin: 25px;
            text-decoration: none;
            transition: 0.4s;
            border-radius: 20px;
            padding-bottom: 20px;
            overflow: hidden;
            box-shadow: 0 10px 15px rgba(0, 0, 0, 0.068);

            @media(min-width:1200px){
                width: 420px;
            }

            @media(min-width:1024px) and (max-width:1200px) {
                width: 300px;
            }

            @media(min-width:768px) and (max-width:1024px) {
                width: 300px;
            }

            @media(min-width:552px) and (max-width:768px) {
                width: 260px;
                margin: 10px;
            }

            @media(min-width:320px) and (max-width:552px) {
                width: 100%;
                margin: 10px;
            }

            &:hover {
                transform: translateY(-10px);
                box-shadow: 0 10px 15px rgba(0, 0, 0, 0.144);
            }

            &:hover .Complex_Img img {
                transform: scale(1.1);
            }

            .Complex_Img {
                width: 100%;

                @media(min-width:1200px) {
                    height: 320px;
                }

                @media(min-width:1024px) and (max-width:1200px) {
                    height: 200px;
                }

                @media(min-width:320px) and (max-width:1024px) {
                    height: 200px;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    transition: 0.4s;
                    transition-delay: .200s;
                }
            }

            .Complex_Info {
                width: 100%;
                height: auto;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                h3 {
                    font-family: "Cormorant Garamond";
                    text-transform: uppercase;
                    color: #000;
                    font-size: 22px;
                    line-height: 1.45em;
                    font-weight: 500;
                    letter-spacing: .1em;
                    margin: 25px 0;
                    -ms-word-wrap: break-word;
                    word-wrap: break-word;
                }

                span {
                    font-family: "Cormorant Garamond";
                    text-transform: uppercase;
                    color: #c28562;
                    font-size: 13px;
                    letter-spacing: .6em;
                    font-family: Raleway;
                    font-weight: 500;
                    margin: 25px 0;
                    -ms-word-wrap: break-word;
                    word-wrap: break-word;
                }
            }
        }
    }
}